@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onDesktop {
  @media (min-width: 1440px) {
    @content;
  }
}

@mixin onTab {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin columnGap($colums, $gap) {
  grid-template-columns: repeat($colums, 1fr);
  column-gap: $gap;
}

@mixin numberfColumns ($first, $last) {
  @for $start from $first through $last {
    @for $end from $first through $last {
      &-#{$start}-#{$end} {
        grid-column: #{$start} / #{$end + 1};
      }
    }
  }
}
